.header
    position: fixed
    height: 90px
    width: 100%
    top: 0
    left: 0
    z-index: 1
    transition: .3s ease-in
    overflow: hidden

    .navbar
        display: flex
        justify-content: space-between
        align-items: center
        max-width: 1240px
        margin: auto
        height: 100%
        padding: 0 1rem

        img
            width: 250px
            height: auto

        .nav-menu
            display: flex

            a
                color: white

            .nav-item
                padding: 1rem
                font-weight: 500

                a:hover
                    padding-bottom: 12px
                    border-bottom: 3px solid var(--secondary-color)

        .humburger
            display: none





@media screen and (max-width:940px)
    .header
        max-width: 100%
        background-color: rgba(0,0,0,.9)

        .navbar
            max-width: 100%

            img
                width: 150px
            
            .humburger
                display: block
                cursor: pointer

            .nav-menu
                position: fixed
                left: -100%
                top: 90px
                flex-direction: column
                background-color: rgba(0,0,0,.9)
                width: 100%
                height: 90vh
                z-index: 999
                text-align: center
                transition: .3s

            .nav-menu.active
                left: 0

                .nav-item
                    margin: 1.5rem 0
    
